.Home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-image: linear-gradient(
    #333 0%,
    #888 40%,
    #7393b3 70%,
    #6495ed 100%
  );
}

.home-header {
  flex-basis: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem;
  line-height: 5.5rem;
  text-align: center;
  animation: zoop ease-in-out 3s;
}

@keyframes zoop {
  from {
    color: #888;
  }
  to {
    color: #fff;
  }
}

.home-h1 {
  font-size: 4rem;
  font-weight: 700;
}

.home-p {
  font-size: 2rem;
  font-weight: 400;
  font-style: italic;
}

.home-nav {
  color: #fff;
  text-shadow: none;
}

.social {
  display: flex;
}

.social-img {
  width: 4rem;
  height: 4rem;
}

.contact {
  margin-top: 1rem;
}

.contact-link {
  font-size: 2rem;
}
